    <li class=\"nav-item\">\n          <router-link\n            :to=\"{ name: 'CreateFilm' }\"\n            class=\"nav-link\"\n            :class=\"$route.path == '/films/crete' ? 'active' : ''\"\n          >\n            Add New Film\n          </router-link>\n        </li>\n      </ul>\n      <form class=\"form-inline my-2 my-lg-0\" @submit.prevent=\"searchMovie\">\n        <input\n          class=\"form-control mr-sm-2\"\n          type=\"search\"\n          v-model=\"search\"\n          title=\"Search Film by name, rating, price, country or genre\"\n          placeholder=\"Search Film by name, rating, price, country or genre\"\n          aria-label=\"Search\"\n          :class=\"{ 'is-invalid': searchError }\"\n        />\n        <button\n          class=\"btn btn-outline-success my-2 my-sm-0\"\n          type=\"button\"\n          @click=\"searchMovie\"\n        >\n          Search\n        </button>\n      </form>\n      <ul class=\"navbar-nav\" v-if=\"!userSession.isLoggedIn\">\n        <li class=\"nav-item\">\n          <router-link\n            :to=\"{ name: 'Login' }\"\n            class=\"nav-link\"\n            :class=\"$route.path == '/login' ? 'active' : ''\"\n          >\n            Login\n          </router-link>\n        </li>\n        <li class=\"nav-item\">\n          <router-link\n            :to=\"{ name: 'Signup' }\"\n            class=\"nav-link\"\n            :class=\"$route.path == '/login' ? 'active' : ''\"\n          >\n            Signup\n          </router-link>\n        </li>\n      </ul>\n      <ul class=\"navbar-nav\" v-if=\"userSession.isLoggedIn\">\n        <li class=\"nav-item mr-2 ml-3 text-danger btn\" @click=\"logout\">\n            Logout\n        </li>\n        <li class=\"nav-item\">\n          <span>\n            Logged In As: \n          </span>\n          <br />\n          <span class=\"text-danger\">\n          {{ userSession.getUser().name }}\n          </span>\n        </li>\n      </ul>\n    </div>\n  </nav>\n  <div class=\"container mt-5\">\n    <RouterView />\n  </div>\n</template>\n<style scoped>\n.container {\n  padding: 0 !important;\n  background: #eee !important;\n  /* margin: 0 !important; */\n}\n</style>"],"sourceRoot":""}]);
